$positive: #389e0d;
$negative: #cf1322;

@mixin transition($property) {
  transition: $property 500ms;
  transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1);
}

@mixin mobile {
  @media (max-width: 575px) {
    @content;
  }
}

@mixin font-sizing {
  font-size: 20px;
  line-height: 28px;
}

.Result {
  padding-bottom: 48px;

  &__Positive {
    .Content {
      font-weight: 600;

      &.Value {
        font-weight: bold;
      }
    }
  }

  &__Negative {
    .Content {
      color: $negative;
    }
  }

  @include mobile {
    padding-bottom: 24px;
  }
}

.Content {
  @include font-sizing;
}

.Value {
  @include font-sizing;

  position: relative;
  white-space: nowrap;
  padding: 0 4px;
  font-size: 24px;

  &::after {
    @include transition(width);

    content: "";
    display: block;
    position: absolute;
    bottom: -4px;
    right: 0;
    height: 4px;
    background-color: $positive;
    width: 100%;
  }

  &.Appear {
    &::after {
      width: 0;
    }

    &__Active {
      &::after {
        width: 100%;
      }
    }
  }
}