$background: #389e0d;
$color: #f6ffed;
$corner: 8px;

@mixin transition($property) {
  transition: $property 500ms;
  transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1);
}

@mixin mobile {
  @media (max-width: 575px) {
    @content;
  }
}

@mixin arrow {
  content: "";
  display: block;
  position: absolute;
  width: 0;
  height: 0;
}

@mixin arrow-up($size, $color) {
  @include arrow;
  @include transition(left);

  border-left: $size solid transparent;
  border-right: $size solid transparent;
  border-bottom: $size solid $color;  
}

@mixin arrow-left($size, $color) {
  @include arrow;

  border-top: $size solid transparent;
  border-bottom: $size solid transparent;
  border-right: $size solid $color;  
}

$top-margin: 28px;

.Advert {
  position: relative;
  margin-top: $top-margin;
  height: calc(100% - #{$top-margin});
  min-height: 112px;
  text-align: center;

  @include mobile {
    margin-top: 0;
    height: auto;
  }

  &.Enter {
    .Container {
      transform: rotate(45deg);
      opacity: 0;

      .Arrow {
        left: 0;
      }

      @include mobile {
        transform: scaleY(0);

        .Arrow {
          left: 50%;
          top: -8px;
        }
      }
    }

    &__Active {
      .Container {
        transform: rotate(0deg);
        opacity: 1;

        .Arrow {
          left: -8px;
        }

        @include mobile {
          transform: scaleY(1);
  
          .Arrow {
            left: 50%;
            top: -8px;
          }
        }
      }
    }
  }

  &.Exit {
    .Container {
      opacity: 1;

      .Arrow {
        left: -8px;
      }

      @include mobile {
        transform: scaleY(1);

        .Arrow {
          left: 50%;
          top: -16px;
        }
      }
    }

    &__Active {
      .Container {
        opacity: 0;

        .Arrow {
          left: 0;
        }

        @include mobile {
          transform: scaleY(0);
  
          .Arrow {
            left: 50%;
            top: -8px;
          }
        }
      }
    }
  }
}

.Container {
  @include transition(all);

  transform-origin: $corner calc(100% - #{$corner});
  position: absolute;
  padding: 16px;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  background: $background;
  border-radius: $corner;
  color: $color;

  .Arrow {
    @include arrow-left(8px, $background);

    transform: translateY(-50%);
    bottom: 40px;
    left: -8px;
  }

  @include mobile {
    height: auto;
    transform-origin: 0 0;
    position: relative;

    .Arrow {
      @include arrow-up(8px, $background);

      transform: translateX(-50%);
      top: -16px;
      left: 50%;
      bottom: auto;
    }
  }
}